<template>
  <div class="add-form">
    <el-dialog
      :title="text + pageTitle"
      :visible.sync="dialogFormVisible"
      width="400px"
    >
      <el-form
        :rules="ruleInline"
        ref="dataForm"
        :model="formBase"
        label-position="left"
        label-width="80px"
      >
        <el-form-item label="学科名称" prop="subjectName">
          <el-input
            v-model="formBase.subjectName"
            size="small"
            placeholder="请输入学科名称"
          ></el-input>
        </el-form-item>
        <el-form-item label="是否显示">
          <el-switch
            v-model="formBase.delivery"
            active-color="#13ce66"
            inactive-color="#ff4949"
          ></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose">{{ $t('table.cancel') }}</el-button>
        <el-button type="primary" @click="createData">{{
          $t('table.confirm')
        }}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { update, add } from '@/api/hmmm/subjects'
export default {
  name: 'usersAdd',
  props: ['text', 'pageTitle', 'formBase', 'ruleInline'],
  data() {
    return {
      dialogFormVisible: false
    }
  },
  computed: {},
  methods: {
    // 弹层显示
    dialogFormV() {
      this.dialogFormVisible = true
    },
    // 弹层隐藏
    dialogFormH() {
      this.dialogFormVisible = false
    },
    // 退出
    handleClose() {
      this.$emit('handleCloseModal')
    },

    // 表单提交
    createData() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          this.$emit('handleCloseModal')
          // const data = {
          //   ...this.formBase
          // }
          if (this.formBase.id) {
            update({
              ...this.formBase,
              isFrontDisplay: this.formBase.delivery
            }).then(() => {
              this.$emit('newDataes', this.formBase)
              this.$message({
                message: '操作成功',
                type: 'success'
              })
            })
          } else {
            add({
              ...this.formBase,
              isFrontDisplay: this.formBase.delivery
            }).then(() => {
              this.$emit('newDataes', this.formBase)
              this.$message({
                message: '操作成功',
                type: 'success'
              })
            })
          }
        } else {
          this.$Message.error('*号为必填项!')
        }
      })
    }
  },
  // 挂载结束

  mounted: function() {},
  // 创建完毕状态
  created() {},
  // 组件更新
  updated: function() {}
}
</script>
<style scoped lang="scss">
.add-form {
  .el-form--label-left .el-form-item__label {
    text-align: right;
  }
  .el-form-item--medium {
    margin-bottom: 22px;
  }
  ::v-deep .el-dialog__footer {
    text-align: right;
  }
}
</style>
