<template>
  <div class="subjects-container">
    <el-card shadow="always">
      <el-row>
        <!-- 搜索 -->
        <el-form :model="requestParameters" ref="requestParameters">
          <el-form-item size="small">
            <label class="el-form-item__label" style="width: 80px"
              >学科名称</label
            >
            <el-input
              @keyup.enter="handleFilter"
              style="width: 194px"
              class="filter-item"
              v-model="requestParameters.subjectName"
            ></el-input>
            <el-button
              class="filter-item"
              size="small"
              @click="clearHandleFilter"
            >
              <span>清除</span>
            </el-button>
            <el-button
              class="filter-item"
              size="small"
              type="primary"
              @click="handleFilter"
            >
              <span>搜索</span>
            </el-button>
            <el-button
              class="filter-item fr"
              size="small"
              style="margin-left: 10px"
              type="success"
              icon="el-icon-edit"
              @click="handleCreate"
            >
              <span>新增学科</span>
            </el-button>
          </el-form-item>
        </el-form>
        <el-alert
          v-if="alertText !== ''"
          :title="alertText"
          type="info"
          class="alert"
          :closable="false"
          show-icon
        ></el-alert>
      </el-row>
      <!-- end -->
      <!-- 数据 -->
      <el-table
        :key="tableKey"
        :data="dataList"
        style="width: 100%"
        fit
        highlight-current-row
      >
        <el-table-column
          type="index"
          :label="$t('table.id')"
          width="48"
          align="left"
          header-align="left"
        >
        </el-table-column>
        <el-table-column
          prop="name"
          label="学科名称"
          min-width="80"
          align="left"
          header-align="left"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.subjectName }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="creator"
          label="创建者"
          align="left"
          header-align="left"
          min-width="80"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.username }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="date"
          label="创建日期"
          min-width="160"
          align="left"
          header-align="left"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.addDate | parseTimeByString }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="creator"
          label="前台是否显示"
          align="left"
          header-align="left"
          min-width="80"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.isFrontDisplay ? '是' : '否' }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="creator"
          label="二级目录"
          align="left"
          header-align="left"
          min-width="80"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.twoLevelDirectory }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="creator"
          label="标签"
          align="left"
          header-align="left"
          min-width="80"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.tags }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="creator"
          label="题目数量"
          align="left"
          header-align="left"
          min-width="80"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.totals }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          align="left"
          header-align="left"
          width="240"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              @click="toDirectorys(scope.row.id, scope.row.subjectName)"
              >学科分类</el-button
            >
            <el-button
              type="text"
              @click="toTags(scope.row.id, scope.row.subjectName)"
              >学科标签</el-button
            >
            <el-button
              type="text"
              v-if="scope.row.status != 'deleted'"
              @click="handleUpdate(scope.row)"
              >修改</el-button
            >
            <el-button type="text" @click="removeUser(scope.row.id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- end -->
      <!-- 分页 -->
      <div class="pagination">
        <PageTool
          :paginationPage="requestParameters.page"
          :paginationPagesize="requestParameters.pagesize"
          :total="total"
          @pageChange="handleCurrentChange"
          @pageSizeChange="handleSizeChange"
        ></PageTool>
      </div>
      <!-- end -->
      <!-- 新增标签弹层 -->
      <component
        v-bind:is="UserAdd"
        ref="editUser"
        :text="text"
        :pageTitle="pageTitle"
        :formBase="formData"
        :ruleInline="ruleInline"
        v-on:newDataes="handleLoadDataList"
        v-on:handleCloseModal="handleCloseModal"
      ></component>
      <!-- end -->
    </el-card>
  </div>
</template>

<script>
import subjectsAdd from './../components/subjects-add.vue'
import PageTool from '../../module-manage/components/page-tool.vue'
import { list, remove, detail } from '@/api/hmmm/subjects.js'
export default {
  name: 'subjects',
  components: {
    PageTool,
    subjectsAdd
  },
  props: {},
  data() {
    return {
      UserAdd: 'subjects-add',
      pageTitle: '学科', // 页面标题
      text: '', // 新增、编辑文本
      tableKey: 0,
      deletedDate: false,
      showDate: true,
      dataList: [],
      total: null,
      listLoading: true,
      dialogStatus: '',
      alertText: '',
      requestParameters: {
        page: 1,
        pagesize: 10,
        subjectName: this.subjectName
      },
      formData: {
        subjectName: '',
        delivery: true
      },
      ruleInline: {
        // 表单验证
        subjectName: [
          { required: true, message: '学科名称不能为空', trigger: 'blur' }
        ]
      }
    }
  },

  computed: {},
  watch: {},
  created() {
    // 读取数据
    this.getList()
    // 键盘enter操作
    var lett = this
    document.onkeydown = function(e) {
      var key = window.event.keyCode
      if (key === 13) {
        lett.handleFilter(this.requestParameters)
      }
    }
  },
  mounted() {},
  methods: {
    // 跳转页面
    toDirectorys(id, username) {
      this.$router.push({
        path: 'directorys',
        query: {
          id,
          name: username
        }
      })
    },
    toTags(id, username) {
      this.$router.push({
        path: 'tags',
        query: {
          id,
          name: username
        }
      })
    },
    // 获取列表数据
    getList() {
      list(this.requestParameters)
        .then(data => {
          this.dataList = data.data.items
          this.total = data.data.counts
          this.alertText = `数据一共 ${this.total} 条`
        })
        .catch(e => {
          this.$message.e('错了哦，这是一条错误消息')
        })
    },
    // 搜索信息
    handleFilter() {
      this.requestParameters.page = 1
      this.getList(this.requestParameters)
      //  const data= await this.getList(this.requestParameters)
      //   console.log('a',data)
    },
    // 清除
    clearHandleFilter() {
      this.requestParameters.subjectName = ''
      // this.getList(this.requestParameters)
      // this.dataList = []
      // this.total = 0
      // this.alertText = `数据一共 ${this.total} 条`
    },
    // 每页显示信息条数
    handleSizeChange(val) {
      this.requestParameters.pagesize = val
      if (this.requestParameters.page === 1) {
        this.getList(this.requestParameters)
      }
    },
    // 进入某一页
    handleCurrentChange(val) {
      this.requestParameters.page = val
      this.getList()
    },

    // 重置
    resetForm() {
      this.$refs.requestParameters.resetFields()
    },
    // 新增学科刷新列表
    handleLoadDataList() {
      this.getList()
    },
    // 数据删除后显示样式
    rowClassStatus(row) {
      if (row.row.is_deleted === 1) {
        this.deletedDate = true
        this.showDate = false
        return 'disabled'
      } else {
        return ''
      }
    },
    // **********************************
    // 搜索的项目
    query() {
      this.formData = {
        subjectName: '',
        delivery: true,
        isFrontDisplay: true
      }
    },
    // 新增学科
    handleCreate() {
      this.query()
      this.text = '新增'
      this.$refs.editUser.dialogFormV()
    },
    // 窗口操作**********************************
    // 弹框关闭
    handleCloseModal() {
      this.$refs.editUser.dialogFormH()
    },
    // 编辑
    // 表单详情数据加载
    hanldeEditForm(objeditId) {
      this.formData.id = objeditId
      detail({ id: objeditId }).then((data) => {
        var datalist = data.data
        this.formData.id = datalist.id
        this.formData.delivery = !!datalist.isFrontDisplay
        this.formData.subjectName = datalist.subjectName
      }).catch(err => console.log(err))
    },
    handleUpdate(row) {
      this.query()
      this.text = '修改'
      this.$refs.editUser.dialogFormV()
      this.hanldeEditForm(row.id)
    },
    // 删除
    removeUser(user) {
      this.$confirm('此操作将永久删除学科 ' + ', 是否继续?', '提示', {
        type: 'warning'
      })
        .then(() => {
          remove({ id: user })
            .then(response => {
              this.$message.success('成功删除了学科' + '!')
              this.dataList.splice(user, 1)
              this.getList(this.requestParameters)
            })
            .catch(response => {
              this.$message.error('删除失败!')
            })
        })
        .catch(() => {
          this.$message.info('已取消操作!')
        })
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.el-button--default {
  margin-left: 10px;
}
.alert {
  margin-bottom: 15px;
}
.pagination {
  margin-top: 10px;
  text-align: right;
}
</style>
<style>
.el-table th {
  background-color: #fafafa;
}
.el-table th.is-leaf {
  border-bottom: 2px solid #e8e8e8;
}

.disabled td {
  background-color: #f9f9f9;
  color: #c1c1c1;
}
.disabled .el-button--primary,
.disabled .el-button--danger {
  background-color: #dbdada;
  border: 1px solid #dbdada;
  color: #999;
  cursor: not-allowed;
}
</style>
